*/

@include media-breakpoint-down(md) {
    h1 {
        font-size: $h1-font-size * .7 !important;
    }
}

.intro-right {
    min-height: 500px;
    background: url('../img/pexels-photo-40120.jpg') center center no-repeat;
    background-size: cover;
}

.credit {
    margin-top: 3rem;
    font-size: 0.8rem;
    color: $gray-500;
}


/*

=====================
SOCIAL LINKS
=====================

*/

.social {
    text-align: left;
    margin-top: 40px;

    a {
        margin: 0 10px 0 0;
        color: #333;
        display: inline-block;
        width: 40px;
        height: 40px;
        border-radius: 20px;
        line-height: 40px;
        font-size: 13px;
        text-align: center;
        @include ondrTransition;
        border: solid 1px #333;

        i {
            vertical-align: bottom;
            line-height: 40px;
        }

        &:hover {
            background: transparent;

            &.facebook {
                color: #4460ae;
                border-color: #4460ae;
            }

            &.youtube {
                color: #c21f25;
                border-color: #c21f25;
            }

            &.twitter {
                color: #3cf;
                border-color: #3cf;
            }

            &.instagram {
                color: #cd4378;
                border-color: #cd4378;
            }

            &.email {
                color: #4a7f45;
                border-color: #4a7f45;
            }

            &.link {
                color: #871AFF;
                border-color: #871AFF;
            }
        }
    }
}